.header {
  position: relative;
  height: 500px;
  width: 100%;
  background-color: #111;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% auto;
  filter: blur(0);
  -webkit-filter: blur(0);
  transition: 0.5s;
  z-index: 1;
  overflow: hidden;
  margin-top: 64px;
}

.headerImBlock {
  display: block;
  position: absolute;
  z-index: 0;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(0px);
  -webkit-filter: blur(0px);
  transition: filter ease 0.5s;
}

.headerImageContainer {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
  opacity: 0;
}

.headerImageContainer img {
  width: 100%;
  height: auto;
}

